/**=====================
    Dark css start
==========================**/


body {
    &.dark {
        background-color: $dark-body;
        transition: all 0.3s ease;
        color: $white-5;
    }
}

.dark {
    h1 {
        color: $white;
    }

    h2 {
        color: $white-1;
    }

    h3 {
        color: $white-2;
    }

    h4 {
        color: $white-3;
    }

    h5 {
        color: $white-4;
    }

    h6 {
        color: $white-5;
    }

    p {
        color: $white-5;
    }

    li {
        color: $white-5;
    }

    a {
        color: $dark-link;
    }

    .modal {
        .btn-close {
            span {
                color: #fff;
            }
        }
    }

    .header-style-7 {
        .main-menu {
            .menu-left {
                .navbar {
                    .bar-style {
                        img {
                            filter: invert(1);
                        }
                    }
                }
            }
        }
    }

    .form-select {
        background-color: $dark-body;
        border-color: $dark-border;
    }

    .shop-sidebar-demo {
        .shop-sidebar {
            background-color: $dark-top;
            border-color: $dark-border;
            .pixelstrap.sm-vertical {
                a {
                    img {
                        filter: invert(1);
                    }
                }
            }
        }
    }

    .bg_cls {
        background-color: $dark-top;
    }

    .white-bg {
        background-color: $dark-body !important;
    }

    .grey-bg {
        background-color: $dark-top;
    }

    .border-top {
        border-color: $dark-border !important;
    }

    .border-top-grey {
        border-color: $dark-border;
    }

    .bg-light {
        background-color: $dark-top !important;
    }

    .blog-section {
        .blog-details {
            p {
                color: $white-5;
            }
        }

        .review-box {
            background-color: $dark-body;

            .review-content {
                h6 {
                    color: $white-5;
                }
            }

            .slick-prev {
                &:after {
                    background-color: $dark-border;
                }
            }
        }
    }

    .page-link {
        background-color: $dark-body;
        transition: all 0.3s ease;
        border-color: $dark-border;
    }

    .breadcrumb-section {
        background-color: $dark-top;
        transition: all 0.3s ease;
    }

    .light-layout {
        background-color: $dark-top;
        transition: all 0.3s ease;
    }

    .form-control {
        color: $white-5;
        background-color: $dark-body;
        transition: all 0.3s ease;
        border: 1px solid $dark-border;
    }

    mark,
    .mark {
        background-color: $dark-top;
        color: $white;
    }

    .product-style-1 {
        &.product-box {
            border-color: $dark-border;
        }
    }

    .addtocart_count {
        .product-box {
            &.product-style-1 {
                .add-button {
                    background-color: $dark-top;
                    color: $dark-link;
                }
            }

            .add-button {
                background-color: $dark-top;
                color: $white;
            }
        }

        .addtocart_btn {
            .cart_qty {
                &.qty-box {
                    .input-group {
                        button {
                            background: #232323 !important;

                            i {
                                color: $white;
                            }
                        }
                    }
                }
            }
        }
    }

    .btn-solid {
        color: $white !important;

        &:hover {
            color: $dark-font !important;
        }
    }

    select {
        color: $white-5;
    }

    option {
        background-color: $dark-body;
        transition: all 0.3s ease;
    }

    header {
        background-color: $dark-body;
        transition: all 0.3s ease;

        &.header-5 {
            .pixelstrap {

                a,
                &:hover,
                &:active {
                    color: $white-5;
                }
            }

            &.left-sidebar-header {
                background-color: $dark-body;

                .top-header {
                    .header-dropdown {
                        >li {
                            img {
                                filter: invert(1);
                            }
                        }
                    }
                }

                .form_search {
                    box-shadow: 0 0 0 1px #545454;
                }

                .onhover-div {
                    >div {
                        img {
                            filter: invert(1);
                        }
                    }
                }
            }
        }

        &.header-metro {
            .metro {
                background-color: $dark-top;
                transition: all 0.3s ease;
            }

            .top-header {
                .header-contact {
                    li {
                        color: $header-font;
                    }
                }

                .header-dropdown {
                    li {
                        color: $header-font;

                        a {
                            i {
                                color: $header-font;
                            }
                        }
                    }
                }
            }
        }

        &.header-tools {
            background-color: transparent;
            transition: all 0.3s ease;

            .logo-menu-part {
                >.container {
                    background-color: $dark-body;
                    transition: all 0.3s ease;
                    border-color: $dark-border;
                }
            }

            &.header-style {
                &.top-relative {
                    background-color: $dark-body;
                }
            }
        }

        &.left-header {
            .top-header {
                background-color: $dark-body;
                transition: all 0.3s ease;
            }

            .main-menu {
                .menu-right {
                    .header-dropdown {
                        >li {
                            img {
                                filter: invert(100);
                            }
                        }
                    }
                }
            }

            .onhover-div {
                >div {
                    img {
                        filter: invert(100);
                    }
                }
            }

            .sidenav {
                nav {
                    background-color: $dark-top;
                    transition: all 0.3s ease;
                }

                .left-sidebar_center {
                    background-color: $dark-top;
                    transition: all 0.3s ease;
                }

                .pixelstrap {
                    >li {
                        >a {
                            background-color: $dark-top;
                            transition: all 0.3s ease;
                        }
                    }
                }
            }

            &.left-header-sm {
                .sidenav {
                    .left-sidebar_center {
                        .pixelstrap {
                            >li {
                                border-color: $dark-border;

                                >a {
                                    img {
                                        background-color: transparent;
                                        filter: invert(1);
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }

        &.green-gradient {
            background-color: $dark-body;
            transition: all 0.3s ease;

            .top-header {
                background-color: $dark-top;
                transition: all 0.3s ease;
                background: $dark-top;
                background-image: none;
            }
        }

        &.header-gym {
            .pixelstrap {
                >li {
                    >a {
                        color: white !important;
                    }
                }
            }
        }

        &.header-style {
            .top-header {
                .header-dropdown {
                    >li {
                        img {
                            filter: invert(1);
                        }
                    }
                }
            }

            .onhover-div {
                >div {
                    img {
                        filter: invert(1);
                    }
                }
            }
        }

        &.left-header {
            &.left-header-relative {
                .pixelstrap {
                    >li {
                        >a {
                            background-color: $dark-body;
                        }
                    }
                }
            }

            .main-menu {
                .brand-logo {
                    border-color: $dark-border;
                }
            }

            .sidenav {
                .leftside_social {
                    border-color: $dark-border;
                }
            }
        }
    }

    .header-compact {
        .main-menu {
            .menu-right {
                .icon-nav {
                    li {
                        &.mobile-cart {
                            > div {
                                img {
                                    filter: invert(1);
                                }
                            }
                        }
                    }
                }
            }
        }
    }

    .header-options {
        span {
            color: $white-4;
        }
    }

    .header-style-1 {
        .navbar {
            .nav-link {
                color: $dark-link;
            }
        }
    }

    .header-style-5 {
        .bottom-part {
            background-color: $dark-top;

            &.bottom-light {
                border-color: $dark-border;
                background-color: transparent;

                .pixelstrap {
                    >li {
                        >a {
                            color: $white-5;

                            &:hover,
                            &:focus {
                                color: $white-5;
                            }
                        }
                    }
                }
            }
        }

        &.style-light {
            .bottom-part {
                .category-menu {
                    .toggle-sidebar {
                        background-color: $dark-body;
                        color: $white-5;
                        border-color: $border-color;

                        h5 {
                            color: $white-5;
                        }
                    }
                }
            }
        }

        &.color-style {
            background-color: $dark-body;

            .top-header {
                &.top-header-theme {
                    background-color: $dark-body;
                    border-color: $dark-border;
                }
            }

            .bottom-part {
                .pixelstrap {
                    &.sm-vertical {
                        >li {
                            >a {
                                color: $dark-link;

                                &:hover,
                                &:focus,
                                &:active {
                                    color: $dark-link;
                                }
                            }
                        }
                    }
                }
            }

            &.style-classic {
                .bottom-part {
                    .pixelstrap {
                        >li {
                            >a {
                                color: $dark-link;

                                &:hover,
                                &:focus,
                                &:active {
                                    color: $dark-link;
                                }
                            }
                        }
                    }

                    .container {
                        background-color: $dark-top;
                    }
                }
            }
        }
    }

    .header-style-7 {
        border-color: $dark-border;
    }

    .product-style-5 {
        &.product-box {
            h4 {
                color: $dark-font;
            }
        }
    }

    .shop-sidebar-demo {
        .shop-main {
            .small-slider {
                .home {
                    .slider-contain {
                        h4, h2 {
                            color: #222222;
                        }
                    }
                }
            }
        }
    }

    .bg-theme {
        background-color: $dark-top;
    }

    &.section-white {
        section {
            background-color: $dark-body;
        }
    }

    .marketplace-sidebar {
        &.sidenav.fixed-sidebar {
            .sm-vertical {
                background-color: $dark-body;
                border-color: $dark-border;
            }
        }

        &.sidenav {
            .sm-vertical {
                background-color: $dark-top;
            }
        }
    }

    .svg-icon-menu {
        &.wo-bg {
            .pixelstrap {
                >li {
                    >a {
                        svg {
                            fill: white;
                        }
                    }
                }
            }
        }

        .pixelstrap {
            >li {
                >a {
                    img {
                        background-color: $dark-body;
                        border-color: $dark-border;
                    }
                }
            }
        }
    }

    .ajax-search {
        .typeahead {
            background-color: $dark-body !important;
        }

        .tt-menu {
            background-color: $dark-body;
        }

        .description-section {
            h4 {
                color: $white-3;
            }
        }
    }

    .product-style-4 {
        &.product-box, &.product-wrap {
            border-color: $dark-border;
        }
    }

    .deal-product-space {
        background-color:  $dark-top;
        .product-style-4 {
            &.product-box, .product-wrap {
                background-color: $dark-body;
            }
        }
    }

    .popular-search-section {
        border-color: $dark-border;
        ul {
            li {
                &:after {
                    background-color: $dark-border;
                }
            }
        }
    }

    .vegetables-category {
        .category-boxes {
            background-color: $dark-top;
            h4 {
                color: #e7eaec;
            }
        }
    }

    .product-vertical {
        .full-box {
            .theme-card {
                .offer-slider {
                    background-color: $dark-body;
                }
            }
        }
    }

    .top-header {
        background-color: $dark-top;
        transition: all 0.3s ease;
    }

    footer {
        &.footer-expand {
            p {
                color: #777;
            }
        }
    }

    .sub-footer {
        background-color: $dark-body;
        transition: all 0.3s ease;
    }

    .border-section {
        border-color: $dark-border;
    }

    .pixelstrap {

        a,
        &:hover,
        &:active {
            color: $white-1;
        }
    }

    .sm-vertical {
        background-color: $dark-top;
        transition: all 0.3s ease;
    }

    .sidenav {
        nav {
            background-color: $dark-top;
            transition: all 0.3s ease;
        }

        .sidebar-back {
            color: $white-1;
            border-color: $dark-border;
        }
    }

    .pixelstrap {
        ul {
            background: $dark-top;
            box-shadow: none;

            a {
                color: $white-3;

                &:hover,
                &:focus,
                &:active,
                &.highlighted {
                    color: $white-3;
                }
            }
        }
    }

    .pixelstrap {

        .home-menu,
        .feature-menu,
        .category-menu,
        .full-mega-menu,
        .clothing-menu {
            box-shadow: 0 0 1px 0 $dark-body;
        }
    }

    .onhover-div {
        >div {
            img {
                filter: brightness(100);
            }
        }

        .show-div {
            background-color: $dark-top;
            transition: all 0.3s ease;
            box-shadow: none;
        }
    }

    .error-section {
        h1 {
            color: #e6e6e6;
        }
    }

    .breadcrumb-item {
        &.active {
            color: $white-3;
        }
    }

    .breadcrumb-section {
        .breadcrumb {
            a {
                color: $grey-about;
            }
        }
    }

    .main-menu {
        .menu-left {
            .navbar {
                i {
                    color: $grey-about;
                }
            }
        }

        .menu-right {
            .icon-nav {
                .onhover-div {
                    .show-div {
                        &.setting {
                            h6 {
                                color: $white-5;
                            }

                            ul {
                                li {
                                    a {
                                        color: $white-5;
                                    }
                                }
                            }
                        }

                        &.shopping-cart {
                            li {
                                .media {
                                    .media-body {
                                        h4 {
                                            color: $white-3;

                                            span {
                                                color: $white-4;
                                            }
                                        }
                                    }
                                }

                                .close-circle {
                                    i {
                                        &:hover {
                                            color: $white;
                                        }
                                    }
                                }

                                .total {
                                    border-color: $dark-border;

                                    h5 {
                                        color: $white-4;
                                    }
                                }

                                .buttons {
                                    a {
                                        color: $dark-link;

                                        &:hover {
                                            color: var(--theme-color);
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }

    .footer-theme {
        .sub-title {
            li {
                color: $white-5;

                a {
                    color: $white-5;
                }
            }

            h4 {
                color: $white-3;
            }
        }
    }

    .footer-social,
    .social-white {
        i {
            color: $white-5;
        }
    }

    .footer-light {
        .subscribe {
            border-color: $dark-border;
        }
    }

    .footer-theme2 {
        .contact-details {
            li {
                color: $white-5;

                a {
                    color: $white-3;
                }
            }
        }

        .footer-link,
        .footer-link-b {
            a {
                color: $dark-link;

                &:hover {
                    color: $white;
                }
            }
        }
    }

    .top-header {
        .header-contact {
            li {
                color: $white-5;
            }
        }

        .header-dropdown {
            li {
                color: $white-5;

                a {
                    i {
                        color: $white-5;
                    }
                }
            }

            .onhover-dropdown {
                .onhover-show-div {
                    li {
                        a {
                            color: $dark-link;
                        }
                    }
                }
            }
        }

        &.top-header-dark {
            .header-dropdown {
                li {
                    a {
                        color: $dark-link;
                    }
                }
            }

            .header-contact {
                li {
                    i {
                        color: $dark-link;
                    }
                }
            }
        }

        &.top-header-dark2 {
            .header-dropdown {
                li {
                    a {
                        color: $white-5;
                    }
                }
            }

            .header-contact {
                li {
                    i {
                        color: $white-5;
                    }
                }
            }
        }

        &.top-header-dark3 {
            .header-dropdown {
                li {
                    a {
                        color: $dark-link;
                    }
                }
            }

            .header-contact {
                li {
                    i {
                        color: $dark-link;
                    }
                }
            }
        }
    }

    .onhover-dropdown {
        .onhover-show-div {
            background-color: $dark-top;
            transition: all 0.3s ease;
            box-shadow: none;
        }
    }

    .testimonial {
        background-color: $dark-top;
        transition: all 0.3s ease;

        .testimonial-slider {
            .slick-track {
                .slick-slide {
                    &:nth-child(even) {
                        .media {
                            border-color: $dark-border;
                        }
                    }
                }
            }

            .media {
                img {
                    border-color: $dark-border;
                }
            }
        }
    }

    .team {
        h2 {
            border-color: $dark-border;
        }
    }

    .service-block {
        +.service-block {
            border-color: $dark-border;
        }
    }

    .pixelstrap {
        &.light-font-menu {
            li {
                >a {
                    color: $white-1;
                }
            }
        }
    }

    .theme-tab {

        .tab-title,
        .tab-title2 {
            a {
                color: $dark-link;
            }

            .current {
                a {
                    color: var(--theme-color);
                }
            }
        }

        .tab-title2 {
            &:after {
                border-color: $dark-border;
            }

            .current {
                background-color: $dark-body;
                transition: all 0.3s ease;

                a {
                    background-color: $dark-body;
                    transition: all 0.3s ease;
                }
            }
        }
    }

    .product-box {
        .cart-info {
            &.bg-color-cls {
                button {
                    background-color: $dark-body;
                }

                a {
                    i {
                        background-color: $dark-body;
                    }
                }
            }
        }
    }

    .product-box,
    .product-wrap {

        .product-detail,
        .product-info {
            h4 {
                color: $white-3;
            }
        }
    }

    .theme-card {
        .offer-slider {
            .media {
                .media-body {
                    h4 {
                        color: $white-3;
                    }
                }
            }
        }

        .slick-prev,
        .slick-next {
            &:before {
                color: $white;
            }
        }
    }

    .theme-card {
        h5 {
            &.title-border {
                border-color: $dark-border;
            }
        }

        &.card-border {
            border-color: $dark-border;
        }
    }

    .dark-layout {
        background-color: $dark-body;
        transition: all 0.3s ease;
    }

    .sub-footer {
        &.darker-subfooter {
            background-color: $dark-top;
            transition: all 0.3s ease;

            p {
                color: $white-5;
            }
        }
    }

    .blog-page {
        .blog-media {
            .blog-right {
                h4 {
                    color: $white-3;
                }

                ul {
                    color: $white-5;

                    li {
                        +li {
                            border-color: $dark-border;
                        }
                    }
                }
            }
        }

        .blog-sidebar {
            .theme-card {
                background-color: $dark-body;
                transition: all 0.3s ease;
                border-color: $dark-border;
            }

            h4 {
                color: $white-3;
            }

            h6 {
                color: $white-5;
            }

            p {
                color: $white-5;
            }
        }
    }

    .blog-detail-page {
        .blog-detail {
            h3 {
                color: $white-2;
            }

            p {
                color: $white-5;
            }
        }

        .post-social {
            color: $white-5;
            border-color: $dark-border;

            li {
                +li {
                    border-color: $dark-border;
                }
            }
        }

        .comment-section {
            border-color: $dark-border;

            li {
                border-color: $dark-border;

                h6 {
                    color: $white-5;

                    span {
                        color: $dark-span;
                    }
                }
            }
        }

        .blog-contact {
            .theme-form {
                label {
                    color: $white-5;
                }

                input,
                textarea {
                    background-color: $dark-body;
                    transition: all 0.3s ease;
                    border-color: $dark-border;
                }
            }
        }
    }

    .form_search {
        background-color: $dark-top;
        box-shadow: none;
    }

    .img-category {
        .img-sec {
            border-color: $dark-border;
        }
    }

    .dark-overlay {
        background-blend-mode: overlay;
        background-color: $dark-top;
    }

    .vector-category {
        .container {
            background-color: $dark-body;
            box-shadow: 0 0 8px #0a0a0a;
        }

        .category-slide {

            .slick-next,
            .slick-prev {
                background-color: $dark-body;
                box-shadow: 0 0 8px #0a0a0a;

                &:before {
                    color: $white;
                }
            }

            .img-category {
                .img-sec {
                    background-color: $dark-top;

                    img {
                        filter: invert(0.6);
                    }
                }
            }
        }
    }

    .cart_counter {
        background: $dark-top;
        box-shadow: none;
        color: $dark-link;
    }

    .cart-section,
    .wishlist-section {
        .cart-table {
            thead {
                th {
                    color: $white-1;
                }
            }
        }

        tbody {
            tr {
                td {
                    border-color: $dark-border !important;

                    a {
                        color: $dark-link;
                    }

                    h2 {
                        color: $white-1;
                    }
                }
            }
        }
    }

    .table {
        tbody {
            +tbody {
                border-color: $dark-border;
            }
        }

        thead {
            th {
                border-color: $dark-border !important;
            }
        }
    }

    .irs-from,
    .irs-to,
    .irs-single {
        color: $dark-link;
    }

    .irs-line {
