        transition: all 0.3s ease;

        .contain-bg {
            background-color: $dark-body;
            transition: all 0.3s ease;

            h4 {
                color: $white-3;
            }

            &:hover {
                h4 {
                    color: var(--theme-color);
                }
            }
        }
    }

    .blog-bg {
        background-color: $dark-border;
        transition: all 0.3s ease;
    }

    .sub-footer {
        &.black-subfooter {
            background-color: $dark-top;
            transition: all 0.3s ease;
        }
    }

    &.tools-bg {
        background-color: $dark-body;
        transition: all 0.3s ease;

        section {
            background-color: $dark-body;
            transition: all 0.3s ease;
        }
    }

    .category-tools {
        .category-m {
            .category-wrapper {
                background-color: $dark-top;
                transition: all 0.3s ease;
            }
        }
    }

    .category-m {
        .category-wrapper {
            border-color: $dark-border;

            h4 {
                color: $white-3;
            }

            .category-link {
                li {
                    a {
                        color: $dark-link;
                    }
                }
            }
        }
    }

    .tools-grey {
        .product-box {
            .product-info {
                background-color: $dark-top;
                transition: all 0.3s ease;
                border-color: rgba($dark-border, 0.2);
            }

            .cart-info {
                button {
                    background-color: $dark-top;
                    transition: all 0.3s ease;
                    color: $dark-link;
                }

                a {
                    background-color: $dark-top;
                    transition: all 0.3s ease;

                    i {
                        color: $dark-link;
                    }
                }
            }

            .img-wrapper {
                .front {
                    background-color: $dark-body;
                    transition: all 0.3s ease;
                }
            }
        }
    }

    .tools-brand {
        .row {
            background-color: $dark-top;
            transition: all 0.3s ease;
            box-shadow: none;
        }
    }

    .typography_section {
        .typography-box {
            .headings {
                background-color: rgba($dark-top, 0.5);
                transition: all 0.3s ease;
                border-color: $dark-border;

                h3 {
                    color: $white-2;
                }

                span {
                    color: $dark-span;

                    code {
                        color: $dark-span;
                    }
                }
            }

            .typo-content {
                .sub-title {
                    color: $white-1;
                    border-color: rgba($dark-border, 0.7);
                }
            }
        }

        .typo-content {
            &.heading_content {

                h1,
                h2,
                h3,
                h4,
                h5,
                h6 {
                    color: white;
                }
            }

            &.product-pagination {
                .pagination {
                    .page-item {
                        border-color: $dark-border;
                    }
                }
            }
        }

        code {
            background-color: $dark-body;
            transition: all 0.3s ease;
        }
    }

    .absolute_banner {
        .collection-banner {
            .absolute-contain {
                background-color: $dark-top;
                transition: all 0.3s ease;
                box-shadow: none;

                h4 {
                    color: $white-3;
                }
            }
        }
    }

    .collection-banner {
        .contain-banner {
            &.content-bg {
                h2 {
                    background-color: $dark-body;
                    color: $white-1;
                }
            }
        }
    }

    .absolute-product {
        .product-box {
            background-color: $dark-top;
            transition: all 0.3s ease;

            .product-detail {
                .cart-bottom {
                    border-color: $dark-border;

                    i {
                        color: $dark-link;

                        &:hover {
                            color: $white;
                        }
                    }
                }
            }
        }
    }

    .box-product {
        .full-box {
            .theme-card {
                .offer-slider {
                    .product-box2 {
                        box-shadow: none;

                        .media {
                            background-color: $dark-top;
                            transition: all 0.3s ease;
                        }

                        .cart-bottom {
                            border-color: $dark-border;
                        }
                    }
                }
            }
        }
    }

    .pets-box {
        .product-box {
            .img-wrapper {
                .cart-info {
                    i {
                        background-color: $dark-body;
                    }
                }
            }
        }
    }

    .insta-title {
        background-color: $dark-body;
        transition: all 0.3s ease;
    }

    .tab-bg {
        background-color: $dark-top;
        transition: all 0.3s ease;
    }

    .darken-layout {
        background-color: $dark-top;
        transition: all 0.3s ease;

        p {
            color: $white-5;
        }

        .sub-title {
            .contact-list {
                li {
                    color: $white-5;
                }

                i {
                    color: $white-5;
                }
            }
        }

        .footer-social {
            i {
                color: $white-5;
            }
        }
    }

    .sub-footer {
        &.dark-subfooter {
            p {
                color: $white-5;
            }
        }
    }

    .cart-section,
    .wishlist-section {
        tbody {
            tr {
                td {

                    a,
                    p {
                        color: $dark-link;
                    }
                }
            }
        }
    }

    .footer-title {
        border-color: $dark-border;
    }

    .category-bg {
        background-color: $dark-top;
        transition: all 0.3s ease;

        .contain-block {
            background-color: $dark-body;
            transition: all 0.3s ease;

            h2 {
                color: $white-1;
            }

            h6 {
                span {
                    color: $dark-span;
                }
            }
        }
    }

    .service_slide {
        .service-home {
            .service-block1 {
                background-color: rgba($dark-top, 0.9);
                transition: all 0.3s ease;

                &:nth-child(even) {
                    background-color: rgba($dark-top, 0.70);
                    transition: all 0.3s ease;
                }

                svg {
                    path {
                        fill: $white;
                    }
                }
            }
        }
    }

    .bg-grey {
        background-color: $dark-top;
        transition: all 0.3s ease;
    }

    .detail-cannabis {
        .detail_section {
            >div {
                background-color: $dark-body;
                transition: all 0.3s ease;
                box-shadow: none;
            }

            svg {
                fill: $white;
            }
        }
    }

    .border-box {
        &.tools-grey {
            .product-box {
                border-color: $dark-border;
            }
        }
    }

    .demo-card {
        &.theme-card {
            background-color: $dark-top;
        }
    }

    .theme-modal {
        .modal-dialog {
            .modal-content {
                .modal-body {
                    background-image: linear-gradient(135deg, var(--theme-color) 5.77%, $dark-top 5.77%, $dark-top 25%, $white-1 25%, $white-1 30.77%, $dark-top 30.77%, $dark-top 50%, var(--theme-color) 50%, var(--theme-color) 55.77%, $dark-top 55.77%, $dark-top 75%, $white-1 75%, $white-1 80.77%, $dark-top 80.77%, $dark-top 100%);

                    .modal-bg {
                        background-color: $dark-body;
                        transition: all 0.3s ease;

                        .age-content {
                            h4 {
                                color: $white-3;
                            }
                        }

                        .btn-close {
                            span {
                                color: $white;
                            }
                        }

                        .offer-content {
                            h2 {
                                color: $white-1;
                            }
                        }
                    }
                }
            }
        }

        &.demo-modal {
            .modal-dialog {
                .modal-content {
                    .modal-body {
                        background-color: $dark-body;
                        transition: all 0.3s ease;

                        .demo-section {
                            .demo-effects {
                                >div {
                                    .layout-container {
                                        box-shadow: none;
                                    }

                                    .demo-text {
                                        h4 {
                                            color: $white-3;
                                        }

                                        .demo-btn {
                                            .btn {
                                                border-color: $dark-border;
                                                background-color: $dark-body;
                                                transition: all 0.3s ease;
                                                color: $dark-link;
                                            }
                                        }
                                    }
                                }
                            }

                            .title-text {
                                h3 {
                                    color: $white-2;
                                }
                            }
                        }
                    }
                }
            }
        }

        &.cart-modal {
            .modal-dialog {
                .modal-content {
                    .modal-body {
                        .modal-bg {
                            &.addtocart {
                                #upsell_product {
                                    .product-box {
                                        .product-detail {
                                            h6 {
                                                a {
                                                    color: $dark-link;
                                                }
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }

        &.exit-modal {
            .media {
                .media-body {
                    h5 {
                        color: $white-4;
                    }
                }
            }

            .stop {
                filter: invert(100);
            }
        }
    }

    #quick-view {
        .modal-dialog {
            .modal-content {
                .modal-body {
                    background-color: $dark-body;
                    transition: all 0.3s ease;
                }
            }
        }
    }

    .addcart_btm_popup {
        background-color: $dark-top;
        transition: all 0.3s ease;
        border-color: $dark-border;

        .fixed_cart {
            i {
                color: $dark-link;
            }
        }
    }

    .review-page {
        .comnt-sec {
            li {
                a {
                    color: $dark-link;
                }
            }
        }
    }

    .sitemap_page {
        ul {
            li {
                a {
                    color: $dark-link;

                    &:hover {
                        color: var(--theme-color);
                    }
                }
            }

            ul {
                li {
                    a {
                        color: $white-4;
                    }
                }
            }
        }

        .row {
            >div {
                &:nth-child(odd) {
                    background-color: $dark-top;
                }
            }
        }
    }

    .effect-cls {

        &:before,
        &:after {
            filter: invert(0.83);
        }
    }

    .game-product {
        .product-box {
            .cart-info {
                a {
                    i {
                        background-color: $dark-body;
                        transition: all 0.3s ease;
                        color: $dark-link;
                    }
                }
            }
        }
    }

    .demo-right {
        a {
            background-color: $black;
            transition: all 0.3s ease;
        }
    }

    .rtl-btn,
    .dark-light {
        background-color: $dark-top;
        box-shadow: none;
    }

    .color-picker {
        a {
            &.handle {
                background-color: $black;
                transition: all 0.3s ease;
                color: $dark-link;
            }
        }
    }

    .setting-box {
        .setting-title {
            h4 {
                color: $font-color;
            }
        }
    }

    .vendor-profile {
        .profile-left {
            background-color: $dark-top;

            .profile-image {
                h3 {
                    color: $white-3;
                }
            }

            .profile-detail {
                border-color: $dark-border;
            }

            .vendor-contact {
                border-color: $dark-border;

                h6 {
                    color: $white-5;
                }
            }
        }

        .collection-product-wrapper {
            .product-top-filter {
                background-color: $dark-top;
            }
        }

        .product-pagination {
            background-color: $dark-top;
        }

        .page-link {
            background-color: $dark-top;
        }

        .profile-title {
            h3 {
                color: $white-2;
            }

            h4 {
                color: $white-3;
            }
        }

        .collection-filter-block {
            border-color: transparent;
            background-color: $dark-top;
        }
    }

    .become-vendor {
        background-color: $dark-top;

        .step-bg {
            .step-box {
                background-color: $dark-body;

                .steps {
                    background-color: $dark-top;
                }
            }
        }
    }

    .dashboard-section {
        .apexcharts-svg {

            text,
            .apexcharts-legend-text {
                fill: $white-2;
                color: $white-2 !important;
            }
        }

        .apexcharts-tooltip {
            &.apexcharts-theme-light {
                background: $dark-body;
                border-color: $dark-border;
                box-shadow: none;

                .apexcharts-tooltip-title {
                    background: $dark-top;
                    border-color: $dark-border;
                }
            }
        }

        .dashboard-sidebar {
            background-color: $dark-top;
        }

        .faq-content {
            .card {
                background-color: $dark-top;
            }
        }

        .counter-section {
            .counter-box {
                background-color: $dark-top;
            }
        }

        .dashboard-table {
            h3 {
                color: $white-2;
            }

            .table {

                th,
                td {
                    color: $white;
                }
            }
        }

        .dashboard-box {
            .dashboard-detail {
                ul {
                    li {
                        .details {
                            .left {
                                h6 {
                                    color: #9c9c9c;
                                }
                            }
                        }
                    }
                }
            }
        }
    }

    .faq-tab {
        .nav-tabs {
            .nav-item {
                .nav-link {
                    background-color: $dark-top;
                    color: $white-5;

                    &.active {
                        background-color: $dark-top;
                    }
                }
            }
        }
    }

    .search-overlay {
        background-color: $dark-body;
    }

    .category-img-wrapper {
        .category-wrap {
            .category-content {
                h3 {
                    color: $white-2;
                }
            }
        }
    }

    .add_to_cart {
        background-color: $dark-body;

        .cart-inner {
            .cart_top {
                border-color: $dark-border;

                h3 {
                    color: $white-2;
                }

                .close-cart {
                    i {
                        color: $dark-link;
                    }
                }
            }

            .cart_media {
                li {
                    .total {
                        border-color: $dark-border;
                    }

                    .media {
                        .media-body {
                            h4 {
                                color: $white-3;
                            }
                        }
                    }
                }
            }
        }

        &.top,
        &.bottom {
            .cart-inner {
                .cart_top {
                    background-color: $dark-top;
                }
            }
        }

        .cart-inner {
            background-color: $dark-body;
        }
    }

    .theme-settings {
        ul {
            li {
                background-color: $dark-top;
                box-shadow: 0px 0px 5px 0px rgba($black, 0.54);
                color: $white;
                a {
                    color: $white;
                }
                .backend-btn {
                    a {
                        color: $white;
                    }
                }
            }
        }
    }

    .setting-sidebar {
        h5 {
            color: #ffffff;
        }
    }

    .search-section {
        select {
            &.form-control {
                background: url(../images/dropdown.png) 95% center no-repeat $dark-body;
            }
        }
    }

    .cycle-box {
        background-color: $dark-body;

        .bottom-detail {
            h4 {
                color: $white;
            }
        }

        .product-detail {
            h4 {
                color: $white-3;
            }

            .details {
                li {
                    color: rgba($white, 0.63);
                }
            }

            .add-wish {
                background-color: $dark-top;
            }
        }

        .cart-detail {
            border-color: $dark-border;

            li {

                button,
                a {
                    color: rgba($white, 0.5);
                }

                svg {
                    color: rgba($white, 0.5);
                }

                +li {
                    border-color: rgba($white, 0.2);
                }
            }
        }

        .img-wrapper {
            &:after {
                background-color: rgba($dark-body, 0.77);
            }
        }
    }

    .center-object-banner {
        .full-banner {
            background-blend-mode: overlay;
            background-color: $dark-body;

            &.feature-banner {
                h2 {
                    color: $white-1;
                }

                .feature-object,
                .feature-object-right {
                    li {
                        .media {

                            h4,
                            p {
                                color: $white-3;
                            }
                        }
                    }
                }
            }
        }
    }

    .cookie-bar {
        &.left-bottom {
            box-shadow: 0 19px 38px rgba(0, 0, 0, 0.12), 0 15px 12px rgba(0, 0, 0, 0.13);
            background-color: $dark-body;

            p {
                color: $white-5;
            }

            .btn-close {
                i {
                    color: $dark-link;
                }
            }
        }
    }

    .mordern-box {
        background-color: $dark-top;

        .mordern-content {
            h6 {
                color: $white-5;
            }

            .mordern-bottom {
                .right {
                    .add-extent {
                        .animated-btn {
                            background-color: $dark-body;
                            color: $dark-link;
                        }

                        .options {
                            background-color: $dark-body;

                            li {
                                i {
                                    color: rgba($dark-link, 0.6);
                                }
                            }
                        }
                    }
                }
            }
        }
    }

    .service-w-bg {
        .service-block {
            .media {
                background-color: $dark-top;
            }
        }
    }

    .title-basic {
        .timer {
            background-color: $dark-top;

            p {
                color: $white-5;
            }
        }
    }

    .product-parallax {
        .theme-card {
            .offer-slider {
                >div {
                    .media {
                        background-color: $dark-body;
                    }
                }
            }
        }
    }

    .load-more-button {
        .btn {
            &.loading {
                border-color:
                    $dark-border $dark-border $dark-border #999;
                background: $dark-body;
            }
        }
    }

    .footer-style-1 {
        .footer-social {
            i {
                background-color: $dark-body;
                border-color: $dark-border;
            }
        }
    }

    .category-width {
        .category-block {
            .category-image {
                img {
                    filter: invert(0.6);
                }
            }

            &:hover {
                .category-image {
                    img {
                        filter: invert(1);
                    }
                }
            }
        }
    }

    .image-swatch {
        li {
            img {
                border-color: $dark-border;
            }

            &.active {
                img {
                    border-color: var(--theme-color);
                }
            }
        }
    }

    .added-notification {
        box-shadow: 0 0 2px 1px rgba(0, 0, 0, 0.63);
    }

    .product-image-360 {
        .nav_bar {
            background-color: $dark-body;
        }
    }

    .gradient-category {
        .gradient-border {
            .img-sec {
                background-color: $dark-top;

                &:after {
                    filter: invert(0.9);
                }
            }
        }
    }

    .deal-category {
        .deal-content {
            background-color: $dark-body;
        }
    }
}

@media (max-width: 1430px) {
    .dark {
        header {
            &.left-header {
                .sidenav {
                    .sidebar-back {
                        color: $white-5;
                        border-color: $dark-border;
                    }
                }
            }
        }

        .collection-product-wrapper {
            .product-top-filter {
                .product-filter-content {
                    .collection-view {
                        border-color: $dark-border;
                    }

                    .product-page-filter {
                        border-color: $dark-border;
                    }
                }
            }
        }
    }
}

@media (max-width: 1199px) {
    .dark {
        .sm-horizontal {
            background-color: $dark-body;
            border-color: $dark-border;

            .mobile-back {
                border-color: $dark-border;
                color: $white-3;
            }

            &.pixelstrap {
                ul {
                    background-color: $dark-body;
                }
            }
        }

        .tab-product {
            .nav-material {
                &.nav-tabs {
                    background-color: $dark-body;
                    border-color: $dark-border;
                }
            }
        }

        header {
            &.left-header {
                .pixelstrap {
                    ul {
                        background-color: $dark-top;
                    }
                }

                &.left-header-relative {
                    .pixelstrap {
                        >li {
                            >a {
                                background-color: $dark-top;
                            }
                        }
                    }
                }
            }

            &.header-christmas {
                .pixelstrap {
                    >li {

                        >a,
                        >a:hover,
                        >a:focus {
                            color: $dark-link !important;
                        }
                    }
                }
            }
        }

        .header-style-1 {
            .bg-light-xl {
                background-color: $dark-top !important;
            }
        }

        .header-style-5 {
            .bottom-part {
                .pixelstrap {
                    >li {
                        >a {
                            color: $dark-link;

                            &:hover,
                            &:focus,
                            &:active {
                                color: $dark-link;
                            }
                        }
                    }
                }

                &.bottom-light {
                    .marketplace-sidebar {
                        &.sidenav {
                            &.fixed-sidebar {
                                background-color: $dark-top;

                                .sm-vertical {
                                    background-color: $dark-top;
                                }
                            }
                        }
                    }
                }
            }

            &.color-style {
                .marketplace-sidebar {
                    &.sidenav {
                        &.fixed-sidebar {
                            background-color: $dark-top;

                            .sm-vertical {
                                background-color: $dark-top;
                            }
                        }
                    }
                }

                &.style-classic {
                    .bottom-part {
                        .container {
                            background-color: transparent;
                        }
                    }
                }
            }
        }

        .marketplace-sidebar {
            &.sidenav {
                &.wo-bg {
                    background-color: $dark-top;
                }
            }
        }
    }
}

@media (max-width: 991px) {
    .dark {
        .collection-filter {
            background-color: $dark-body;
        }

        .collection-product-wrapper {
            .product-top-filter {
                .product-filter-content {

                    .product-page-per-view,
                    .product-page-filter {
                        select {
                            border-color: $dark-border;
                        }
                    }
                }

                .popup-filter {

                    .product-page-filter,
                    .product-page-per-view {
                        border-color: $dark-border;
                    }
                }
            }
        }

        .collection-mobile-back {
            border-color: $dark-border;

            span {
                color: $dark-span;
            }
        }

        .collection-collapse-block {
            border-color: $dark-border !important;
        }

        .collection-filter {
            box-shadow: none;
        }

        .contact-page {
            .contact-right {
                ul {
                    li {
                        border-color: $dark-border;
                    }
                }
            }
        }

        .dashboard-left {
            background-color: $dark-top;
            box-shadow: none;
        }

        header {
            &.header-tools {
                .logo-menu-part {
                    background-color: $dark-body;
                }
            }

            &.header-style {
                &.header-tools {
                    .top-header {
                        background-color: $dark-body;
                    }
                }
            }
        }

        .service_slide {
            .service-home {
                .service-block1 {
                    background-color: $dark-body;

                    &:nth-child(even) {
                        background-color: $dark-body;
                    }
                }
            }
        }

        .dashboard-section {
            .dashboard-sidebar {
                .faq-tab {
                    .nav-tabs {
                        background-color: $dark-top;
                    }
                }
            }
        }
    }
}

@media (max-width: 767px) {
    .dark {
        .collection-product-wrapper {
            .product-pagination {
                .theme-paggination-block {
                    nav {
                        border-color: $dark-border;
                    }
                }
            }
        }

        .product-pagination {
            .pagination {
                border-color: $dark-border;
            }
        }

        .product-wrapper-grid {
            &.list-view {
                .product-box {
                    border-color: $dark-border;
                }
            }
        }

        .banner-timer {
            .banner-text {
                background-color: rgba($black, 0.65);
            }
        }

        .border-box {
            &.tools-grey {
                .product-box {
                    .cart-info {

                        a,
                        button {
                            background-color: $dark-body;
                        }

                        a {
                            i {
                                opacity: 0.4;
                            }
                        }
                    }
                }
            }
        }

        footer {
            &.footer-5 {
                .dark-layout {
                    .footer-title {
                        border-color: $dark-border;
                    }
                }
            }
        }
    }
}

@media (max-width: 577px) {
    .dark {
        header {
            &.header-style {
                .top-header {
                    .header-dropdown {
                        >li {
                            img {
                                filter: invert(0.55);
                            }
                        }
                    }
                }
            }
        }

        .header-5 {
            .top-header {
                .header-dropdown {

                    .mobile-wishlist,
                    .mobile-account {
                        img {
                            filter: invert(1);
                            opacity: 0.7;
                        }
                    }
                }
            }
        }

        .collection-product-wrapper {
            .product-top-filter {
                .product-filter-content {
                    .product-page-per-view {
                        border-color: $dark-border !important;
                    }
                }
            }
        }

        .tools-service {
            &.absolute-banner {
                .absolute-bg {
                    box-shadow: none;
                }
            }
        }

        .full-banner {
            .banner-contain {
                background-color: rgba(0, 0, 0, 0.2);
            }
        }

        .mobile-fix-option {
            background-color: $dark-body;
        }
    }
}

@media (max-width: 420px) {
    .dark {
        .full-box {
            .theme-card {
                .offer-slider {
                    .sec-1 {
                        .product-box2 {
                            border-color: $dark-border;
                        }
                    }
                }
            }

            .center-slider {
                .offer-slider {
                    .product-box {
                        border-color: $dark-border;
                    }
                }
            }
        }
    }
}